import { Middleware, MiddlewareAPI, Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import {
  selectTreatment,
  deselectTreatment,
  selectAdditionalTreatment,
  deselectAdditionalTreatment,
  selectTreatmentBookingIds,
} from '../../booking/forces/actions';
import { getAvailability } from './actions';

type Action =
  | ReturnType<typeof selectTreatment>
  | ReturnType<typeof deselectTreatment>
  | ReturnType<typeof selectAdditionalTreatment>
  | ReturnType<typeof deselectAdditionalTreatment>;

const listenerFunctionTypes = [
  selectTreatment.toString(),
  deselectTreatment.toString(),
  selectAdditionalTreatment.toString(),
  deselectAdditionalTreatment.toString(),
  selectTreatmentBookingIds.toString(),
];

export const treatmentSelectionMiddleware: Middleware = ({
  dispatch,
}: MiddlewareAPI<Dispatch<any>, RootState>) => (next: Dispatch) => (action: Action) => {
  next(action);
  if (listenerFunctionTypes.includes(action.type)) {
    const appointmentId = (action as ReturnType<typeof selectTreatment>).payload.appointmentId;
    dispatch(getAvailability({ appointmentId }));
  }

  return;
};
