import * as React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import { getSnackbarMessage } from '../forces/selectors';
import Modal from '../../../common/components/modal/Modal';
import { H2 } from '../../../common/components/texts';
import styles from './Snackbar.module.css';

type OwnProps = {};
type DispatchProps = ReturnType<typeof mapDispatchToProps>;
type StateProps = ReturnType<typeof mapStateToProps>;
type Props = OwnProps & React.HTMLAttributes<HTMLElement> & StateProps & DispatchProps;

const Snackbar: React.FunctionComponent<Props> = ({ className, snackbarMessage, ...props }) => {
  const hasSnackbarMessage = snackbarMessage != null;
  return (
    <Modal isOpen={hasSnackbarMessage}>
      <H2 className={styles.text}>{snackbarMessage}</H2>
    </Modal>
  );
};

const mapStateToProps = (state: RootState) => ({
  snackbarMessage: getSnackbarMessage(state),
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators({}, dispatch);

export default connect<StateProps, DispatchProps, OwnProps, RootState>(
  mapStateToProps,
  mapDispatchToProps
)(Snackbar);
