export enum FieldTypes {
  Treatment = 'treatment',
  Note = 'note',
}

export type TreatmentNote = AdditionalFieldBase & {
  note: string;
};

export type Treatment = AdditionalFieldBase & {
  title: string;
  displayTitle: string;
  type: FieldTypes;
  price: number;
  duration: number;
  description: string;
  bookingServiceId: string;
  slug: string;
  multiple: false;
  additionalFields: Array<AdditionalField>;
};
export type TreatmentNormalized = Treatment & {
  additionalFields: Array<string>;
};

export interface TreatmentTypesEntitiesNormalized {
  treatments: {
    [key: string]: TreatmentNormalized;
  };
  treatmentTypes: {
    [key: string]: TreatmentTypeNormalized;
  };
}
export type TreatmentTypeResultNormalized = Array<string>;
export interface TreatmentTypesNormalized {
  entities: TreatmentTypesEntitiesNormalized;
  result: TreatmentTypeResultNormalized;
}

export interface TreatmentType<T = Treatment> {
  id: string;
  title: string;
  hidden: boolean;
  treatments: Array<T>;
}
export interface TreatmentTypeNormalized extends TreatmentType<string> {}

interface AdditionalFieldBase {
  id: string;
  type: FieldTypes;
}
export type AdditionalField = Treatment; // Todo: Add Note back in
