import * as schemas from './schemas';
import { RootState } from '../../../root.reducer';
import { denormalize } from 'normalizr';
import { createSelector } from 'reselect';
import { TreatmentType, Treatment } from './types';
import { calculateTotalPrice, calculateTotalDuration } from './treatment.utils';
import { Service } from '../../user/forces/types';
import { getSelectedTreatmentIds } from '../../booking/forces/selectors';

export const getTreatmentTypes = createSelector(
  (state: RootState) => state.treatmentTypes.treatmentTypes.result,
  (state: RootState) => state.treatmentTypes.treatmentTypes.entities,
  (result, entities) =>
    denormalize(result, schemas.treatmentTypes, entities) as Array<TreatmentType>
);
export const isTreatmentTypesLoading = (state: RootState) => state.treatmentTypes.isLoading;
export const getTreatmentsNormalized = (state: RootState) => {
  return state.treatmentTypes.treatmentTypes.entities.treatments;
};

export const getTreatmentsForServiceIds = (serviceIds: Array<string>) =>
  createSelector(
    (state: RootState) => state.treatmentTypes.treatmentTypes.entities,
    entities => {
      const treatments = denormalize(serviceIds, schemas.treatments, entities) as Array<Treatment>;
      return treatments.filter(treatment => treatment != null);
    }
  );

export const getTotalPrice = (services: Array<Service>) => (state: RootState) => {
  return calculateTotalPrice(services);
};
export const getTotalDuration = (services: Array<Service>) => (state: RootState) => {
  return calculateTotalDuration(services);
};

export const isAllSelectedTreamentTypesHidden = (state: RootState) => {
  const selectedTreatmentIds = getSelectedTreatmentIds(state);
  const treatmentTypes = state.treatmentTypes.treatmentTypes.result.map(
    id => state.treatmentTypes.treatmentTypes.entities.treatmentTypes[id]
  );
  const treatmentTypesWithTreatmentIds = treatmentTypes.filter(treatmentType => {
    const hasTreatment = treatmentType.treatments.some(treatmentId =>
      selectedTreatmentIds.includes(treatmentId)
    );
    return hasTreatment;
  });
  if (treatmentTypesWithTreatmentIds.length === 0) {
    return false;
  }
  const isAllTreamentTypesHidden = treatmentTypesWithTreatmentIds.every(treatmentType => {
    return treatmentType.hidden;
  });
  return isAllTreamentTypesHidden;
};
