import { APIRequest } from '../../../root.types';
import { Treatment } from '../../treatment/forces/types';
import { mapTreatmentsToBookingServiceIds } from '../../clinic/forces/api.utils';

const createBooking = (request: APIRequest) => async (parameters: {
  token: string;
  clinicId: string;
  time: number;
  date: number;
  treatments: Array<Treatment>;
  usePoints: boolean;
}) => {
  const { token, clinicId, treatments, time, date, usePoints } = parameters;
  const bookingServiceIds = mapTreatmentsToBookingServiceIds(treatments);
  return request({
    method: 'post',
    path: `/clinics/${clinicId}/bookings`,
    headers: {
      Authorization: token,
    },
    body: JSON.stringify({
      time,
      date,
      services: bookingServiceIds,
      usePoints,
      treatments,
      clinicId,
    }),
  });
};

const updateBooking = (request: APIRequest) => async (parameters: {
  bookingId: string;
  token: string;
  clinicId: string;
  time: number;
  date: number;
  treatments: Array<Treatment>;
  usePoints: boolean;
}) => {
  const { bookingId, token, clinicId, treatments, time, date, usePoints } = parameters;
  const bookingServiceIds = mapTreatmentsToBookingServiceIds(treatments);
  const body = JSON.stringify({
    time,
    date,
    services: bookingServiceIds,
    usePoints,
  });
  const path = `/users/current/bookings/${clinicId}/${bookingId}`; // `/clinics/${clinicId}/bookings`;
  return request({
    method: 'put',
    path,
    headers: {
      Authorization: token,
    },
    body,
  });
};

export default {
  createBooking,
  updateBooking,
};
