import { MiddlewareAPI, Dispatch } from 'redux';
import { RootState } from '../../../root.reducer';
import {
  selectTreatment,
  deselectTreatment,
  selectDate,
  selectTime,
  bookAppointmentSuccess,
} from '../../booking/forces/actions';
import Analytics from './service';
import { EventTypes, Action } from './types';
import { formatToDateAndYear, formatDurationToTime } from '../../../utils/date.utils';
import config from '../../../config';
import { newMemberRegistered, paymentInfoRegistered } from '../../user/forces/actions';

type ActionType =
  | ReturnType<typeof selectTreatment>
  | ReturnType<typeof deselectTreatment>
  | ReturnType<typeof selectTime>
  | ReturnType<typeof selectDate>
  | ReturnType<typeof bookAppointmentSuccess>
  | ReturnType<typeof newMemberRegistered>
  | ReturnType<typeof paymentInfoRegistered>;

export const analyticsMiddleware = (middleware: MiddlewareAPI<Dispatch<any>, RootState>) => (
  next: Dispatch
) => (action: ActionType) => {
  // TODO: Fix type inference
  if (action.type === selectTreatment.toString()) {
    const payload = (action as ReturnType<typeof selectTreatment>).payload;
    const trackData = {
      event: EventTypes.BookingUserEvent,
      action: Action.SelectedTreatment,
      label: payload.treatment.title,
    };

    Analytics.trackEvent(trackData);
  }

  if (action.type === deselectTreatment.toString()) {
    const payload = (action as ReturnType<typeof deselectTreatment>).payload;
    const trackData = {
      event: EventTypes.BookingUserEvent,
      action: Action.DeselectedTreatment,
      label: payload.treatment.title,
    };
    Analytics.trackEvent(trackData);
  }

  if (action.type === selectTime.toString()) {
    const payload = (action as ReturnType<typeof selectTime>).payload;
    const trackData = {
      event: EventTypes.BookingUserEvent,
      action: Action.SelectedTime,
      label: formatDurationToTime(payload.time),
    };
    Analytics.trackEvent(trackData);
  }

  if (action.type === selectDate.toString()) {
    const payload = (action as ReturnType<typeof selectDate>).payload;
    const trackData = {
      event: EventTypes.BookingUserEvent,
      action: Action.SelectedDate,
      label: formatToDateAndYear(payload.date),
    };
    Analytics.trackEvent(trackData);
  }

  if (action.type === bookAppointmentSuccess.toString()) {
    const { treatments, trackData } = (action as ReturnType<typeof bookAppointmentSuccess>).payload;
    const { userData, isMember, date, time, userIpAddress } = trackData;
    const dateBooked = formatToDateAndYear(date);
    const timeBooked = formatDurationToTime(time);
    const treatmentsNameList = treatments.map(treatment => treatment.title).join(', ');
    const treatmentsTotalPrice = treatments
      .map(treatment => treatment.price)
      .reduce((total, current) => total + current);
    const userId = isMember ? userData._id : null;

    const bookingData = {
      userId,
      name: userData.name,
      email: userData.email,
      phoneNumber: userData.phoneNumber,
      product: treatmentsNameList,
      revenue: treatmentsTotalPrice,
      location: config.defaultClinicId,
      ipAddress: userIpAddress,
      points: 0,
      dateBooked,
      timeBooked,
    };
    if (isMember) {
      bookingData.points = userData.points;
    }

    const bookingEventType = isMember ? EventTypes.BookingMember : EventTypes.BookingGuest;
    const bookingMemberOrGuest = {
      event: bookingEventType,
      ...bookingData,
    };

    const bookingMemberAndGuest = {
      event: EventTypes.Booking,
      ...bookingData,
    };

    Analytics.trackEvent(bookingMemberOrGuest);
    Analytics.trackEvent(bookingMemberAndGuest);
  }

  if (action.type === newMemberRegistered.toString()) {
    const { trackData } = (action as ReturnType<typeof newMemberRegistered>).payload;
    const { _id, name, email, phoneNumber, points } = trackData.user;
    const memberData = {
      event: EventTypes.NewMember,
      userId: _id,
      name,
      email,
      phoneNumber,
      points,
      ipAddress: trackData.userIpAddress,
    };

    Analytics.trackEvent(memberData);
  }

  if (action.type === paymentInfoRegistered.toString()) {
    const { trackData } = (action as ReturnType<typeof paymentInfoRegistered>).payload;
    const { _id, name, email, phoneNumber, points } = trackData.user;
    const memberData = {
      event: EventTypes.MemberAddsCc,
      userId: _id,
      name,
      email,
      phoneNumber,
      points,
      ipAddress: trackData.userIpAddress,
    };

    Analytics.trackEvent(memberData);
  }

  return next(action);
};
