import { Treatment, FieldTypes, TreatmentType, TreatmentNote } from '../../treatment/forces/types';

const mapToAdditionalFields = (additionalFields: Array<any>) => {
  return additionalFields.map(additionalField => {
    if (additionalField._type === FieldTypes.Treatment) {
      return mapToTreatment(additionalField);
    }
    // TODO: Implement FieldTypes.Note
    // } else if (additionalField._type === FieldTypes.Note) {
    //   return mapToNote(additionalField);
    // }
    throw new Error(`${additionalField._type} is not supported`);
  });
};

const mapToNote = (data: any): TreatmentNote => ({
  id: data.id,
  type: data._type,
  note: data.note,
});

const mapToTreatment = (obj: any): Treatment => {
  return {
    id: obj._id,
    slug: obj.slug,
    title: obj.title,
    displayTitle: obj.displayTitle,
    type: FieldTypes.Treatment,
    price: obj.price,
    duration: obj.duration,
    description: obj.description,
    bookingServiceId: obj.bookingServiceId,
    multiple: obj.multiple ? obj.multiple : false,
    additionalFields: mapToAdditionalFields(obj.additionalFields || []),
  };
};

export const mapToTreatmentTypes = (response: Array<any>): Array<TreatmentType> => {
  return response.map(obj => ({
    id: obj._id,
    title: obj.title,
    treatments: obj.treatments.map(mapToTreatment),
    hidden: obj.hidden || false,
  }));
};
