import { Dispatch, AnyAction } from 'redux';
import { ThunkDispatch } from 'redux-thunk';
import { createAction } from 'deox';
import { RootState } from '../../../root.reducer';
import { ThunkArguments } from '../../../root.types';
import { getToken, getCurrentUser } from '../../user/forces/selectors';
import { Booking, UserRole } from '../../user/forces/types';
import api from './api';
import { getBooking } from './selectors';
import { fetchCurrentUserSuccess } from '../../user/forces/actions';
import { getAllTreatmentTypes } from '../../treatment/forces/actions';

export const fetchBookingsRequest = createAction('BOOKINGS_GET_REQUEST');
export const fetchBookingsSuccess = createAction(
  'BOOKINGS_GET_SUCCESS',
  handler => (bookings: Array<Booking>) => {
    return handler({ bookings });
  }
);
export const fetchBookingsError = createAction('BOOKINGS_GET_ERROR', handler => (error: Error) => {
  return handler({ error });
});
export const fetchBookings = () => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    dispatch(fetchBookingsRequest());
    try {
      const token = getToken(getState());
      if (token == null) {
        throw new Error('token is not set');
      }

      const bookings = await api.fetchBookings(request)({
        token,
      });
      dispatch(fetchBookingsSuccess(bookings));
    } catch (err) {
      dispatch(fetchBookingsError(err));
    }
  };
};

export const fetchBookingRequest = createAction('BOOKING_GET_REQUEST');
export const fetchBookingSuccess = createAction(
  'BOOKING_GET_SUCCESS',
  handler => (booking: Booking) => {
    return handler({ booking });
  }
);
export const fetchBookingError = createAction('BOOKING_GET_ERROR', handler => (error: Error) => {
  return handler({ error });
});
export const fetchBookingIfNeeded = ({ id, waitlistId }: { waitlistId: string; id: string }) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const state = getState();
    const booking = getBooking(state, id);
    if (booking != null) {
      // Booking exists in state, so return
      return;
    }

    const token = getToken(state);
    if (token == null) {
      throw new Error('token is not set');
    }

    try {
      dispatch(fetchBookingRequest());
      const booking = await api.fetchBooking(request)({ token, waitlistId, id });
      dispatch(fetchBookingSuccess(booking));
      return booking;
    } catch (err) {
      dispatch(fetchBookingError(err));
    }
  };
};

export const cancelBookingRequest = createAction('BOOKING_CANCEL_REQUEST');
export const cancelBookingSuccess = createAction(
  'BOOKING_CANCEL_SUCCESS',
  handler => (bookingId: string) => {
    return handler({ bookingId });
  }
);
export const cancelBookingError = createAction(
  'BOOKING_CANCEL_ERROR',
  handler => (error: Error) => {
    return handler({ error });
  }
);
export const cancelBooking = ({ waitlistId, id }: { waitlistId: string; id: string }): Promise => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    try {
      const state = getState();
      const token = getToken(state);
      if (token == null) {
        throw new Error('token is not set');
      }

      dispatch(cancelBookingRequest());
      const cancelBookingResponse = await api.cancelBooking(request)({ token, waitlistId, id });
      dispatch(cancelBookingSuccess(id));

      const user = getCurrentUser(state);

      if (user && user.role === UserRole.Member) {
        dispatch(fetchCurrentUserSuccess(cancelBookingResponse));
      }
      return cancelBookingResponse;
    } catch (err) {
      dispatch(cancelBookingError(err));
      throw new Error(err.message);
    }
  };
};
