import { normalize } from 'normalizr';
import { AnyAction } from 'redux';
import { ThunkDispatch } from 'redux-thunk';
import { createAction } from 'deox';
import { ThunkArguments } from './../../../root.types';
import { RootState } from '../../../root.reducer';
import {
  TreatmentType,
  TreatmentTypesNormalized,
  TreatmentTypesEntitiesNormalized,
  TreatmentTypeResultNormalized,
} from './types';
import { getAllSanityTreatmentTypes } from '../../sanity/forces/api';
import * as schemas from './schemas';

export const getAllTreatmentTypesRequest = createAction('GET_ALL_TREATMENT_TYPES_REQUEST');
export const getAllTreatmentTypesSuccess = createAction(
  'GET_ALL_TREATMENT_TYPES_SUCCESS',
  resolve => (treatmentTypesNormalized: TreatmentTypesNormalized) => {
    return resolve({ treatmentTypesNormalized });
  }
);
export const getAllTreatmentTypesError = createAction(
  'GET_ALL_TREATMENT_TYPES_ERROR',
  resolve => (error: Error) => {
    return resolve({ error });
  }
);

export const getAllTreatmentTypes = () => {
  return async (dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>) => {
    dispatch(getAllTreatmentTypesRequest());
    try {
      const treatmentTypes = await getAllSanityTreatmentTypes();
      const treatmentTypesNormalized = normalize<
        Array<TreatmentType>,
        TreatmentTypesEntitiesNormalized,
        TreatmentTypeResultNormalized
      >(treatmentTypes, schemas.treatmentTypes);
      dispatch(getAllTreatmentTypesSuccess(treatmentTypesNormalized));
    } catch (err) {
      dispatch(getAllTreatmentTypesError(err));
    }
  };
};
