import { APIRequest } from '../../../root.types';
import { User, UserResponse } from './types';
import config from '../../../config';

const createUser = (request: APIRequest) => (parameters: {
  name: string;
  email: string;
  phoneNumber: string;
  password: string;
  referralCode: string;
}) => {
  return request<{ token: string }>({
    method: 'post',
    path: '/auth/register/user',
    body: JSON.stringify(parameters),
  });
};

const fetchBillingInformation = (request: APIRequest) => async (parameters: { token: string }) => {
  const { token } = parameters;
  return request({
    method: 'get',
    path: '/users/current/billing',
    headers: {
      Authorization: token,
    },
  });
};

const registerBillingInformation = (request: APIRequest) => async (parameters: {
  token: string;
  cardToken: any;
}) => {
  const { token, cardToken } = parameters;
  return request({
    method: 'post',
    path: '/auth/register/billing',
    headers: {
      Authorization: token,
    },
    body: JSON.stringify({ token: cardToken }),
  });
};

const updateBillingInformation = (request: APIRequest) => async (parameters: {
  token: string;
  cardToken: any;
}) => {
  const { token, cardToken } = parameters;
  return request({
    method: 'put',
    path: '/users/current/billing',
    headers: {
      Authorization: token,
    },
    body: JSON.stringify({ token: cardToken }),
  });
};

const fetchCurrentUser = (request: APIRequest) => async (parameters: { token: string }) => {
  const { token } = parameters;
  return request<User>({
    method: 'get',
    path: '/users/current',
    headers: {
      Authorization: token,
    },
  });
};

const loginMember = (request: APIRequest) => async (parameters: {
  phoneNumber: string;
  password: string;
}) => {
  const { phoneNumber, password } = parameters;
  return request<UserResponse>({
    method: 'post',
    path: '/auth/signin/user',
    body: JSON.stringify({ phoneNumber, password }),
  });
};

const loginGuest = (request: APIRequest) => async ({ userId }: { userId: string }) => {
  return request<UserResponse>({
    method: 'post',
    path: '/auth/signin/guest',
    body: JSON.stringify({
      id: userId,
    }),
  });
};

const createGuest = (request: APIRequest) => async ({
  phoneNumber,
  name,
  email,
}: {
  phoneNumber: string;
  name: string;
  email: string;
}) => {
  return request({
    method: 'post',
    path: '/auth/signin/guest',
    body: JSON.stringify({
      phoneNumber,
      name,
      email,
    }),
  });
};

const updateProfileInformation = (request: APIRequest) => async ({ ...params }) => {
  const { token } = params;
  return request({
    method: 'put',
    path: '/users/current/general',
    headers: {
      Authorization: token,
    },
    body: JSON.stringify(params),
  });
};

const updateMembershipStatus = (request: APIRequest) => async ({ ...params }) => {
  const { token } = params;
  return request({
    method: 'patch',
    path: '/users/current/membership',
    headers: {
      Authorization: token,
    },
    body: JSON.stringify(params),
  });
};

const changePassword = (request: APIRequest) => async (parameters: {
  currentPassword: string;
  newPassword: string;
  token: string;
}) => {
  const { currentPassword, newPassword, token } = parameters;
  return request({
    method: 'put',
    path: '/users/current/password',
    headers: {
      Authorization: token,
    },
    body: JSON.stringify({ currentPassword, newPassword }),
  });
};

const setPrivacyPolicyStatusMember = (request: APIRequest) => async ({ ...params }) => {
  const { token } = params;
  return request({
    method: 'put',
    path: '/users/current/general',
    headers: {
      Authorization: token,
    },
    body: JSON.stringify(params),
  });
};

const setPrivacyPolicyStatusGuest = (request: APIRequest) => async ({ ...params }) => {
  const { id } = params;
  const path = `/auth/guest/${id}/general`;
  return request({
    method: 'put',
    path,
    headers: {
      Authorization: config.apiKey,
    },
    body: JSON.stringify(params),
  });
};

export default {
  createUser,
  fetchBillingInformation,
  registerBillingInformation,
  updateBillingInformation,
  fetchCurrentUser,
  createGuest,
  loginMember,
  loginGuest,
  updateProfileInformation,
  updateMembershipStatus,
  changePassword,
  setPrivacyPolicyStatusMember,
  setPrivacyPolicyStatusGuest,
};
