import 'whatwg-fetch';
import reduxProvider from './src/common/store/reduxProvider';
import './src/assets/styles/main.module.css';
import './src/assets/styles/snipcart.css';
import * as lang from './src/assets/no.json';

export const onClientEntry = () => {
  // IntersectionObserver polyfill for gatsby-background-image (Safari, IE)
  if (typeof window.IntersectionObserver === `undefined`) {
    import(`intersection-observer`);
    console.log(`# IntersectionObserver is polyfilled!`);
  }
  window.onload = () => {
    Snipcart.api.session.setLanguage('no', lang.default)
  }
};

export const onPreRouteUpdate = async() => {
  const windowGlobal = typeof window !== 'undefined' && window;
  if (windowGlobal) {
    const snipcartBackButton = document.getElementsByClassName('snipcart-modal__close-title');
    if (snipcartBackButton.length > 0) {
      snipcartBackButton[0].click();
    }
  }
}

export const wrapRootElement = reduxProvider;
