export enum UserRole {
  Guest = 'guest',
  Member = 'member',
}

export type UserResponse = {
  isActive: boolean;
  isFinished: boolean;
  email: string;
  points: number;
  _id: string;
  name: string;
  role: UserRole;
  phoneNumber: string;
  bookings: Array<{
    duration: number;
    services: Array<string>;
    _id: string;
    bookingId: string;
    waitlistId: string;
    time: number;
  }>;
  createdAt: Date;
  updatedAt: Date;
  referralCode: string;
  token: string;
  paymentInfo: {
    last4Digits?: number;
    cardId: string;
    customerId: string;
    _id: string;
  } | null;
  privacyPolicyStatus: boolean | undefined;
};

export type User = {
  id: string;
  name: string;
  isActive: boolean;
  isFinished: boolean;
  points: number;
  referralCode: string;
  phoneNumber: string;
  email: string;
  paymentInfo: {
    last4Digits?: number;
    cardId: string;
    customerId: string;
    _id: string;
  } | null;
  role: UserRole;
  bookings: Array<Booking>;
  cancellationDate: Date;
  privacyPolicyStatus: boolean | undefined;
};

export type Booking = {
  id: string;
  duration: number;
  time: number;
  timeOfDayinMilliseconds: number;
  waitlistId: string;
  serviceIds: Array<Service>;
};

export interface Service {
  id: string;
  title: string;
  price: number;
  duration: number;
  name?: string;
}

export enum RegistrationStep {
  GeneralForm,
  Billing,
  Summary,
}

export interface CreateGuestResponse {
  token: string;
}
