export const windowExist = () => typeof window !== 'undefined' && window;
export const convertClinicIdToName = (clinicId: string | null) => {
  switch (clinicId) {
    case 'oliotest':
      return 'VikaTest';
    case 'valkyrientest':
      return 'ValkyirenTest';
    case 'oliovika':
      return 'Vika';
    case 'valkyrien':
      return 'Valkyrien';
    default:
      return '';
  }
};
