import { createAction } from 'deox';
import { ThunkDispatch } from 'redux-thunk';
import { RootState } from '../../../root.reducer';
import { ThunkArguments } from '../../../root.types';
import { AnyAction } from 'redux';

const delay = (ms: number) => new Promise(resolve => setTimeout(resolve, ms));

export const setSnackbarMessage = createAction('UI_SHOW_SNACKBAR', handler => (message: string) => {
  return handler({ message });
});
export const hideSnackbar = createAction('UI_HIDE_SNACKBAR');

export const showSnackbar = (message: string, duration = 3000) => {
  return async (dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>) => {
    dispatch(setSnackbarMessage(message));
    await delay(duration);
    dispatch(hideSnackbar());
  };
};
