import { createSelector } from 'reselect';
import { RootState } from '../../../root.reducer';
import { BookingSection, BookingSectionIDs, IButtonState } from './types';
import {
  calculateTotalPrice,
  calculateTotalDuration,
} from '../../treatment/forces/treatment.utils';
import { Treatment, AdditionalField } from '../../treatment/forces/types';
import { getTreatmentsForServiceIds } from '../../treatment/forces/selectors';
import { isLoggedIn, isUserGuest } from '../../user/forces/selectors';
import BOOKING_TEXTS from '../assets/texts';
import config from '../../../config';

export const getSelectedTreatmentIds = (state: RootState) => state.booking.treatments.selectedIds;
export const getSelectedTreatments = (state: RootState) => {
  const selectedTreatmentIds = getSelectedTreatmentIds(state);
  const treatmentsByServiceIds = getTreatmentsForServiceIds(selectedTreatmentIds)(state);
  return treatmentsByServiceIds;
};
export const hasSelectedClinic = (state: RootState) => (state.booking.waitlistId ? true : false);
export const getSelectedClinicId = (state: RootState) => state.booking.waitlistId;
export const getSelectedDate = (state: RootState): number | null => state.booking.date;
export const getSelectedTime = (state: RootState) => state.booking.time;
export const getCustomerInfo = (state: RootState) => state.booking.customerInfo;
export const hasSelectedTreatments = (state: RootState) => {
  return getSelectedTreatments(state).length > 0;
};
export const hasSelectedAdditionalField = (state: RootState) => (treatment: Treatment) => {
  return treatment.additionalFields.some(additionalField => {
    return getSelectedTreatmentIds(state).includes(additionalField.bookingServiceId);
  });
};
export const isTreatmentSelected = (state: RootState) => (treatment: Treatment) => {
  return getSelectedTreatmentIds(state).includes(treatment.bookingServiceId);
};
export const isAdditionalFieldSelected = (state: RootState, additionalField: AdditionalField) => {
  return getSelectedTreatmentIds(state).includes(additionalField.bookingServiceId);
};
export const numberOfSpecificTreatment = (state: RootState) => (treatment: Treatment) => {
  return getSelectedTreatmentIds(state).filter(
    treatmentId => treatmentId === treatment.bookingServiceId
  ).length;
};

export const hasCustomerInfo = (state: RootState) => {
  return Object.values(getCustomerInfo(state)).every(value => value != '');
};
export const hasDateAndTime = (state: RootState) => {
  return hasDate(state) && hasTime(state);
};
export const hasDate = (state: RootState) => {
  return state.booking.date != null;
};
export const hasTime = (state: RootState) => {
  return state.booking.time != null;
};
export const hasPaymentInfo = (state: RootState) => {
  return (state.user.user && state.user.user.paymentInfo) != null;
};
export const getBooking = (state: RootState) => {
  return state.booking;
};
export const getBookingUI = (state: RootState) => {
  return state.booking.ui;
};
export const shouldShowBillingForm = (state: RootState) => {
  const { showAddedPaymentInfoSuccess } = state.booking.ui;
  const isPaymentInfoSet = hasPaymentInfo(state);
  const isUserLoggedIn = isLoggedIn(state);
  const editMode = getEditMode(state);

  // Don't show billing form in edit mode
  if (editMode) {
    return false;
  }

  // Don't show billing form for guests
  if (!isUserLoggedIn) {
    return false;
  }

  // Show payment form when paymentinfo has recently been added
  if (isPaymentInfoSet && showAddedPaymentInfoSuccess) {
    return true;
  }

  // Don't show payment form when the payment info is already set
  if (isPaymentInfoSet && !showAddedPaymentInfoSuccess) {
    return false;
  }
  return true;
};
export const getUsePoints = (state: RootState) => state.booking.usePoints;
export const getEditMode = (state: RootState): boolean => state.booking.editMode;
export const hasOneValidBookingSection = (
  state: RootState,
  bookingSections: Array<BookingSection>
) => {
  return bookingSections.map(section => section.sectionValidator).some(func => func(state));
};
export const IsLastBookingSectionOnValidBooking = (state: RootState) => {
  const lastSection = state.booking.ui.bookingSectionSelected === BookingSectionIDs.Overview;
  const validBooking = isBookingValid(state);
  return lastSection && validBooking;
};
export const getBookingButtonState = (
  state: RootState,
  bookingSections: BookingSection[]
): IButtonState => {
  const { booking } = state;
  const lastSectionAndValid = IsLastBookingSectionOnValidBooking(state);
  const btnSubmitText = state.booking.editMode
    ? BOOKING_TEXTS.BUTTONS.UPDATE
    : BOOKING_TEXTS.BUTTONS.BOOK;
  const buttonText =
    isBookingValid && booking.ui.bookingSectionSelected === BookingSectionIDs.Overview
      ? btnSubmitText
      : BOOKING_TEXTS.BUTTONS.NEXT;
  const isEnabled = hasOneValidBookingSection(state, bookingSections);
  return {
    text: buttonText,
    isUpdate: booking.editMode,
    isReady: lastSectionAndValid,
    isEnabled,
  };
};
export const getSubmissionError = (state: RootState) =>
  state.booking.submission.error ? state.booking.submission.error.message : null;
export const getCancelBookingError = (state: RootState) =>
  state.bookings.error ? state.bookings.error.message : null;
export const getTotalPrice = (state: RootState) => {
  return calculateTotalPrice(getSelectedTreatments(state));
};
export const getTotalDuration = (state: RootState) => {
  return calculateTotalDuration(getSelectedTreatments(state));
};
export const getTotalPriceOfAdditionalTreatments = (state: RootState) => (treatment: Treatment) => {
  const { additionalFields } = treatment;

  const selectedTreatmentIds = getSelectedTreatmentIds(state);
  const selectedAdditionalFields = additionalFields.filter(additionalField =>
    selectedTreatmentIds.includes(additionalField.bookingServiceId)
  );

  if (selectedAdditionalFields.length === 0) {
    return treatment.price;
  }

  const totalPriceOfAdditionalTreatments = selectedAdditionalFields.reduce(
    (previousValue, additionalField) => {
      return previousValue + additionalField.price;
    },
    0
  );

  return totalPriceOfAdditionalTreatments;
};
export const isSubmitting = (state: RootState) => state.booking.submission.isLoading;

export const isBookingValid = createSelector(
  hasSelectedTreatments,
  hasDateAndTime,
  hasCustomerInfo,
  hasPaymentInfo,
  isLoggedIn,
  isUserGuest,
  (
    hasSelectedTreatments,
    hasDateAndTime,
    hasCustomerInfo,
    hasPaymentInfo,
    isLoggedIn,
    isUserGuest
  ) => {
    if (config.bookingDisabled) {
      return false;
    }
    const equalityCheck = (selector: boolean) => selector === true;
    if (isLoggedIn) {
      let checks = [hasSelectedTreatments, hasDateAndTime, hasPaymentInfo];
      checks = isUserGuest ? [hasSelectedTreatments, hasDateAndTime] : checks;
      return checks.every(equalityCheck);
    } else {
      return [hasSelectedTreatments, hasDateAndTime, hasCustomerInfo].every(equalityCheck);
    }
  }
);
