import { createReducer } from 'deox';
import { setSnackbarMessage, hideSnackbar } from './actions';

interface State {
  snackbar: {
    message: string | null;
  };
}

const defaultState: State = {
  snackbar: {
    message: null,
  },
};

const appReducer = createReducer(defaultState, handle => [
  handle(setSnackbarMessage, (state, { payload }) => ({
    ...state,
    snackbar: {
      message: payload.message,
    },
  })),
  handle(hideSnackbar, state => ({
    ...state,
    snackbar: {
      message: null,
    },
  })),
]);

export default appReducer;
