import { AnyAction } from 'redux';
import { ThunkDispatch } from 'redux-thunk';
import { createAction } from 'deox';
import { ThunkArguments } from '../../../root.types';
import { RootState } from '../../../root.reducer';
import { getToken, hasToken, getCurrentUser } from './selectors';
import api from './api';
import storage from '../../../utils/storage.utils';
import { ReactStripeElements } from 'react-stripe-elements';
import { User, UserResponse } from './types';
import { navigate } from 'gatsby';
import { mapToUser, mapToCreateGuestResponse } from './mapper';
import { getUserIpAddress } from '../../../utils/network.utils';

const tokenKey = 'olio_token';

const storeToken = (token: string) => storage.setItem(tokenKey, token);
const getStoredToken = () => storage.getItem(tokenKey);
const removeStoredToken = () => storage.removeItem(tokenKey);

export const setStoredTokenIfNeeded = () => {
  return (dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>) => {
    const storedToken = getStoredToken() as string | null;
    if (storedToken != null) {
      dispatch(setUserToken(storedToken));
    }
  };
};

export const setUserToken = createAction('USER_SET_TOKEN', resolve => (token: string) => {
  return resolve({ token });
});
export const logoutSuccess = createAction('USER_LOGOUT_SUCCESS');

export const createUser = (params: {
  name: string;
  email: string;
  phoneNumber: string;
  password: string;
  referralCode: string;
}) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const createUserResponse = await api.createUser(request)(params);
    storeToken(createUserResponse.token);
    dispatch(setUserToken(createUserResponse.token));
    await dispatch(fetchCurrentUser());
    const user = getCurrentUser(getState());
    const userIpAddress = await getUserIpAddress();
    const trackData = { user, userIpAddress };
    dispatch(newMemberRegistered(trackData));
    return createUserResponse;
  };
};

export const newMemberRegistered = createAction(
  'NEW_MEMBER_REGISTERED',
  resolve => (trackData: any) => {
    return resolve({ trackData });
  }
);

export const setPaymentInformation = ({ cardToken }: { cardToken: any }) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const token = getToken(getState());
    if (token == null) {
      throw new Error('You need to be logged in');
    }
    await api.registerBillingInformation(request)({
      token,
      cardToken,
    });
    await dispatch(fetchCurrentUser());
    const user = getCurrentUser(getState());
    const userIpAddress = await getUserIpAddress();
    const trackData = { user, userIpAddress };
    dispatch(paymentInfoRegistered(trackData));
  };
};

export const paymentInfoRegistered = createAction(
  'PAYMENT_INFO_REGISTERED',
  resolve => (trackData: any) => {
    return resolve({ trackData });
  }
);

export const updatePaymentInformation = ({ cardToken }: { cardToken: any }) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const state = getState();
    const token = getToken(state);
    const user = getCurrentUser(state);
    if (token == null || user == null) {
      throw new Error('You need to be logged in');
    }

    if (user.isActive) {
      await api.updateBillingInformation(request)({ token, cardToken });
    } else {
      await api.registerBillingInformation(request)({ token, cardToken });
    }
    await dispatch(fetchCurrentUser());
  };
};

export const getPaymentInformation = () => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const token = getToken(getState());
    if (token == null) {
      throw new Error('You need to be logged in');
    }
    await api.fetchBillingInformation(request)({
      token,
    });
    await dispatch(fetchCurrentUser());
  };
};

export const fetchCurrentUserIfNeeded = () => {
  return (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState
  ) => {
    if (hasToken(getState())) {
      return dispatch(fetchCurrentUser());
    }
    return Promise.resolve();
  };
};

export const fetchCurrentUserRequest = createAction('USER_FETCH_CURRENT_REQUEST');
export const fetchCurrentUserSuccess = createAction(
  'USER_FETCH_CURRENT_SUCCESS',
  handler => (user: User) => {
    return handler({ user });
  }
);
export const fetchCurrentUserError = createAction(
  'USER_FETCH_CURRENT_ERROR',
  handler => (error: Error) => {
    return handler({ error });
  }
);

export const fetchCurrentUser = () => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    dispatch(fetchCurrentUserRequest());
    try {
      const token = getToken(getState());
      if (token == null) {
        throw new Error('token is not set');
      }

      const user = await api.fetchCurrentUser(request)({
        token,
      });
      dispatch(fetchCurrentUserSuccess(user));
    } catch (err) {
      dispatch(fetchCurrentUserError(err));
    }
  };
};

export const getStripeToken = (stripe: ReactStripeElements.StripeProps) => {
  return async () => {
    const payload = await stripe.createToken();
    if (payload.error != null) {
      new Error(`Failed with error code ${payload.error.decline_code}`);
    }
    return payload.token;
  };
};

export const loginRequest = createAction('USER_LOGIN_REQUEST');
export const loginSuccess = createAction('USER_LOGIN_SUCCESS', handler => (user: User) => {
  return handler({ user });
});
export const loginError = createAction('USER_LOGIN_ERROR', handler => (error: Error) => {
  return handler({ errorMessage: error.message || 'Something went wrong' });
});

const loginUser = async (
  loginApiFunction: () => Promise<UserResponse>,
  shouldStoreTokenInLocalstorage: boolean
) => {
  return async (dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>) => {
    try {
      dispatch(loginRequest());
      const userResponse = await loginApiFunction();
      const user = mapToUser(userResponse);
      if (shouldStoreTokenInLocalstorage) {
        storeToken(userResponse.token);
      }
      dispatch(setUserToken(userResponse.token));
      dispatch(loginSuccess(user));
    } catch (err) {
      dispatch(loginError(err));
      throw err;
    }
  };
};

export const loginMember = (parameters: { phoneNumber: string; password: string }) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request, routes }: ThunkArguments
  ) => {
    await dispatch(await loginUser(() => api.loginMember(request)(parameters), true));
    navigate(routes.Dashboard());
  };
};

export const loginGuest = (userId: string) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request, routes }: ThunkArguments
  ) => {
    // Pass false to don't store the token in localstorage as we don't want the user to be
    // logged in the next time they return to the application
    await dispatch(await loginUser(() => api.loginGuest(request)({ userId }), false));
    navigate(routes.Bookings());
  };
};

export const createGuest = (name: string, phoneNumber: string, email: string) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const response = await api.createGuest(request)({ name, phoneNumber, email });
    return mapToCreateGuestResponse(response);
  };
};

export const logout = () => {
  return async (dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>) => {
    removeStoredToken();
    dispatch(logoutSuccess());
  };
};

export const updateProfileInformation = ({ ...params }) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    params.token = getToken(getState());
    await api.updateProfileInformation(request)(params);
    await dispatch(fetchCurrentUser());
  };
};

export const changeUserPassword = (parameters: {
  currentPassword: string;
  newPassword: string;
}) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const state = getState();
    const token = getToken(state);
    const user = getCurrentUser(state);

    if (token == null || user == null) {
      throw new Error('You need to be logged in');
    }
    const { currentPassword, newPassword } = parameters;
    await api.changePassword(request)({ currentPassword, newPassword, token });
  };
};

export const updateMembershipStatus = () => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const parameters = { token: getToken(getState()) };
    await api.updateMembershipStatus(request)(parameters);
    await dispatch(fetchCurrentUser());
  };
};

export const setPrivacyPolicyStatus = (privacyPolicyStatus: boolean) => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request }: ThunkArguments
  ) => {
    const token = getToken(getState());
    const user = getCurrentUser(getState());
    if (user && user.role === 'member') {
      await api.setPrivacyPolicyStatusMember(request)({ token, privacyPolicyStatus });
    } else if (user && user.role === 'guest') {
      await api.setPrivacyPolicyStatusGuest(request)({
        token,
        privacyPolicyStatus,
        guestUserId: user.id,
      });
    }
    await dispatch(fetchCurrentUser());
  };
};
