import { RootState } from '../../../root.reducer';

export const getBookings = (state: RootState) => state.bookings.bookings;
export const isBookingsLoading = (state: RootState) => state.bookings.isLoading;
export const getBooking = (state: RootState, id: string) => {
  return getBookings(state).find(booking => booking.id === id);
};
