import { createReducer } from 'deox';
import {
  getAllTreatmentTypesRequest,
  getAllTreatmentTypesSuccess,
  getAllTreatmentTypesError,
} from './actions';
import { TreatmentTypesNormalized } from './types';

interface State {
  isLoading: boolean;
  error: Error | null;
  treatmentTypes: TreatmentTypesNormalized;
}

const defaultState: State = {
  isLoading: false,
  error: null,
  treatmentTypes: {
    entities: {
      treatments: {},
      treatmentTypes: {},
    },
    result: [],
  },
};

const treatmentsReducer = createReducer(defaultState, handle => [
  handle(getAllTreatmentTypesRequest, state => ({
    ...defaultState,
    isLoading: true,
  })),
  handle(getAllTreatmentTypesSuccess, (state, { payload }) => ({
    treatmentTypes: { ...payload.treatmentTypesNormalized },
    isLoading: false,
    error: null,
  })),
  handle(getAllTreatmentTypesError, (state, { payload }) => ({
    ...defaultState,
    error: payload.error,
  })),
]);

export default treatmentsReducer;
