import { combineReducers } from 'redux';
import bookingReducers from './domain/booking/forces/reducers';
import bookingsReducers from './domain/bookings/forces/reducers';
import clinicReducer from './domain/clinic/forces/reducers';
import userReducer from './domain/user/forces/reducers';
import treatmentTypesReducers from './domain/treatment/forces/reducers';
import registerReducer from './domain/register/forces/reducers';
import appReducer from './domain/app/forces/reducers';
import uiReducer from './domain/ui/forces/reducers';

const reducers = combineReducers({
  app: appReducer,
  booking: bookingReducers,
  bookings: bookingsReducers,
  clinic: clinicReducer,
  user: userReducer,
  register: registerReducer,
  treatmentTypes: treatmentTypesReducers,
  ui: uiReducer,
});

export type RootState = ReturnType<typeof reducers>;

export default reducers;
