import { createReducer } from 'deox';
import { getAvailabilityRequest, getAvailabilitySuccess, getAvailabilityError } from './actions';
import { AvailableDatesResponse } from './types';

interface State {
  isLoading: boolean;
  error: Error | null;
  availability: AvailableDatesResponse;
}

const defaultState: State = {
  isLoading: false,
  error: null,
  availability: {
    firstAvailableDay: null,
    availableDays: {},
  },
};

const clinicReducer = createReducer(defaultState, handle => [
  handle(getAvailabilityRequest, state => ({
    error: null,
    isLoading: true,
    availability: {
      ...state.availability,
    },
  })),
  handle(getAvailabilitySuccess, (state, { payload }) => ({
    error: null,
    isLoading: false,
    availability: {
      firstAvailableDay:
        state.availability.firstAvailableDay || payload.availableDatesResponse.firstAvailableDay,
      availableDays: {
        ...state.availability.availableDays,
        ...payload.availableDatesResponse.availableDays,
      },
    },
  })),
  handle(getAvailabilityError, (state, { payload }) => ({
    ...defaultState,
    error: payload.error,
  })),
]);

export default clinicReducer;
