import { createStore, applyMiddleware } from 'redux';
import thunk from 'redux-thunk';
import { composeWithDevTools } from 'redux-devtools-extension/developmentOnly';
import rootReducer, { RootState } from '../../root.reducer';
import request from '../api/request';
import { ThunkArguments } from '../../root.types';
import config from '../../config';
import { treatmentSelectionMiddleware } from '../../domain/clinic/forces/middlewares';
import { analyticsMiddleware } from '../../domain/analytics/forces/middlewares';
import errorTrackingMiddleware from '../errorTracking/errorTrackingMiddleware';
import Routes from '../routing/Routes';

const configureStore = (initialState?: Partial<RootState>) => {
  const apiRequest = request(config.apiBaseUrl, config.apiKey);
  const store = createStore(
    rootReducer,
    initialState,
    composeWithDevTools(
      applyMiddleware(
        thunk.withExtraArgument<ThunkArguments>({ request: apiRequest, config, routes: Routes }),
        treatmentSelectionMiddleware,
        analyticsMiddleware,
        errorTrackingMiddleware
      )
    )
  );

  return store;
};

export default configureStore;
