import * as React from 'react';
import posed, { PoseGroup } from 'react-pose';
import Portal from '../portal/Portal';
import styles from './Modal.module.css';

const Container = posed.div({
  enter: { y: 0, opacity: 1 },
  exit: { y: -100, opacity: 0 },
});

const Backdrop = posed.div({
  enter: { opacity: 1 },
  exit: { opacity: 0 },
});

type Props = { isOpen: boolean } & React.HTMLAttributes<HTMLElement>;

const Modal: React.FunctionComponent<Props> = ({ className, isOpen, children, ...props }) => {
  return (
    <Portal>
      <PoseGroup flipMove={false}>
        {isOpen && [
          <Backdrop key="backdrop" className={styles.backDrop} />,
          <Container key="modal" className={styles.container} {...props}>
            <div className={styles.innerContainer}>{children}</div>
          </Container>,
        ]}
      </PoseGroup>
    </Portal>
  );
};

export default Modal;
