import { APIRequest } from '../../../root.types';
import { mapResponseToBookings, mapResponseToBooking } from './mappers';

const fetchBookings = (request: APIRequest) => async ({ token }: { token: string }) => {
  const response = await request<Array<{ [key: string]: any }>>({
    method: 'get',
    path: '/users/current/bookings',
    headers: {
      Authorization: token,
    },
  });
  return mapResponseToBookings(response);
};

const fetchBooking = (request: APIRequest) => async ({
  token,
  waitlistId,
  id,
}: {
  token: string;
  waitlistId: string;
  id: string;
}) => {
  const response = await request<Array<{ [key: string]: any }>>({
    method: 'get',
    path: `/users/current/bookings/${waitlistId}/${id}`,
    headers: {
      Authorization: token,
    },
  });
  return mapResponseToBooking(response);
};

const cancelBooking = (request: APIRequest) => async ({
  token,
  waitlistId,
  id,
}: {
  token: string;
  waitlistId: string;
  id: string;
}) => {
  const response = await request<Array<{ [key: string]: any }>>({
    method: 'delete',
    path: `/users/current/bookings/${waitlistId}/${id}`,
    headers: {
      Authorization: token,
    },
  });
  return response;
};

export default {
  fetchBookings,
  fetchBooking,
  cancelBooking,
};
