export const scrollToBookingSection = (id: BookingSectionIDs) => {
  const offset = getBookingSectionScrollOffset(id);
  Scroll.scroller.scrollTo(id, {
    smooth: true,
    offset,
  });
};

const formatBookingSections = (bookingSection: BookingSection, state: RootState) => {
  return {
    ...bookingSection,
    isValid: bookingSection.sectionValidator(state),
  };
};

export const validateAndScrollBookingSections = (bookingSections: Array<BookingSection>) => {
  return (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState
  ) => {
    const state = getState();
    const selectedBookingSection: string = state.booking.ui.bookingSectionSelected;
    const validatedBookingSections = bookingSections.map(bookingSection =>
      formatBookingSections(bookingSection, state)
    );

    const scrollAndSetTo = (id: BookingSectionIDs) => {
      scrollToBookingSection(id);
      dispatch(setBookingSection(id));
    };

    const firstInvalidSection = validatedBookingSections.find(
      bookingSection => !bookingSection.isValid
    );
    const overviewSectionIsSelected: boolean =
      selectedBookingSection === BookingSectionIDs.Overview;

    // @ISSUE - Book button persists if you scroll away from overviewSection while its selected
    const scrollToLastBookingSection: boolean = !firstInvalidSection && !overviewSectionIsSelected;

    if (firstInvalidSection) {
      scrollAndSetTo(firstInvalidSection.id);
    }
    if (scrollToLastBookingSection) {
      scrollAndSetTo(bookingSections[bookingSections.length - 1].id);
    }
  };
};

