export const bookAppointmentRequest = createAction('BOOKING_BOOK_APPOINTMENT_REQUEST');
export const bookAppointmentSuccess = createAction(
  'BOOKING_BOOK_APPOINTMENT_SUCCESS',
  resolve => (treatments: Array<Treatment>, trackData: any) => {
    return resolve({ treatments, trackData });
  }
);
export const bookAppointmentError = createAction(
  'BOOKING_BOOK_APPOINTMENT_ERROR',
  resolve => (error: Error) => {
    return resolve({ error });
  }
);

const getBookingToken = () => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState
  ) => {
    const state = getState();
    const isUserLoggedIn = isLoggedIn(state);

    // Create a guest user if the user is not logged in
    if (!isUserLoggedIn) {
      const { name, phoneNumber, email } = getCustomerInfo(state);
      const { token } = await dispatch(createGuest(name, phoneNumber, email));
      return token;
    }
    const token = getToken(state);
    return token;
  };
};

export const bookAppointment = () => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request, config, routes }: ThunkArguments
  ) => {
    dispatch(bookAppointmentRequest());
    try {
      const state = getState();
      const token = await dispatch(getBookingToken());
      const time = getSelectedTime(state);
      const date = getSelectedDate(state);
      const treatments = getSelectedTreatments(state);
      const clinicId = getSelectedClinicId(state);
      const usePoints = getUsePoints(state);
      const userIpAddress = await getUserIpAddress();
      const user = getCurrentUser(state);
      const isMember = user ? true : false;
      const customerInfo = getCustomerInfo(state);
      const userData = isMember ? user : customerInfo;
      const trackData = { userData, isMember, date, time, userIpAddress };

      if (date == null || time == null || clinicId == null) {
        throw new Error('Date, time or location cannot be null');
      }

      if (token == null) {
        throw new Error('You need to be logged in');
      }

      await api.createBooking(request)({
        token,
        date,
        time,
        treatments,
        clinicId,
        usePoints,
      });

      dispatch(bookAppointmentSuccess(treatments, trackData));
      navigate(routes.BookingSuccess());
    } catch (err) {
      dispatch(bookAppointmentError(err));
    }
  };
};

export const updateAppointment = () => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request, config, routes }: ThunkArguments
  ) => {
    try {
      const state = getState();
      const token = await dispatch(getBookingToken());
      const time = getSelectedTime(state);
      const date = getSelectedDate(state);
      const treatments = getSelectedTreatments(state);
      const clinicId = getSelectedClinicId(state);
      const usePoints = getUsePoints(state);

      const bookingId = state.booking.id;

      if (!bookingId) {
        throw new Error('No booking selected');
      }

      if (!clinicId) {
        throw new Error('No clinic selected');
      }

      if (date == null || time == null) {
        throw new Error('Date and time cannot be null');
      }

      if (token == null) {
        throw new Error('You need to be logged in');
      }

      await api.updateBooking(request)({
        bookingId,
        token,
        date,
        time,
        treatments,
        clinicId,
        usePoints,
      });
      navigate(routes.BookingSuccess());
    } catch (err) {
      dispatch(
        bookAppointmentError(
          new Error(
            'Something went wrong when updatinng your booking... Please confirm you entered your details correctly.'
          )
        )
      );
    }
  };
};

export const resetBookingForm = createAction('BOOKING_RESET_FORM');
export const prepopulateBookingWithQueryParameters = (locationSearch: string | null) => {
  return async (dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>) => {
    if (locationSearch == null) {
      return;
    }
    const { name, phoneNumber, treatments: treatmentBookingIds } = parseQueryString(locationSearch);
    if (typeof name === 'string' && typeof phoneNumber === 'string') {
      dispatch(updateCustomerInfo({ name, phoneNumber }));
    }
    if (treatmentBookingIds instanceof Array && treatmentBookingIds.length > 0) {
      await dispatch(getAllTreatmentTypes());
      dispatch(
        selectTreatmentBookingIds({
          treatmentBookingIds,
        })
      );
    }
  };
};

export const populateBookingWithExistingBooking = (bookingId: string): Promise => {
  return async (
    dispatch: ThunkDispatch<RootState, ThunkArguments, AnyAction>,
    getState: () => RootState,
    { request, config, routes }: ThunkArguments
  ) => {
    const state = getState();
    const booking = getBooking(state, bookingId);
    if (!booking) {
      return;
    }
    const clinicId = booking.waitlistId;
    await dispatch(fetchBookingIfNeeded({ id: bookingId, waitlistId: clinicId }));
    if (booking) {
      await dispatch(getAllTreatmentTypes()); // @TODO - Get allTreatmentTypesIfNeeded - Sjekk om de er cache
      const treatmentBookingIds = booking.serviceIds.map((service: Service) => service.id);
      dispatch(
        selectTreatmentBookingIds({
          treatmentBookingIds: treatmentBookingIds ? treatmentBookingIds : [],
        })
      );
      dispatch(
        populateExistingBooking(
          true,
          bookingId,
          clinicId,
          booking.timeOfDayinMilliseconds,
          booking.time
        )
      );
    }
  };
};
