import { formatToAPIDate } from '../../../utils/date.utils';
import { AvailableDatesResponse } from './types';

const sortSlotTimes = (slots: Array<{ time: number; availability: number }>) => {
  return slots.sort((a, b) => a.time - b.time);
};

export const mapResponseToAvailableDates = (responseData: any): AvailableDatesResponse => {
  const firstAvailable = responseData.find(obj => obj.isOpen);
  const availableDays = responseData.reduce((previousValue, currentValue) => {
    if (currentValue.isOpen) {
      const dateKey = formatToAPIDate(currentValue.date);
      previousValue[dateKey] = {
        ...currentValue,
        slots: sortSlotTimes(currentValue.slots),
      };
    }
    return previousValue;
  }, {});

  const availableDatesResponse = {
    firstAvailableDay: firstAvailable ? firstAvailable.date : null,
    availableDays,
  };
  return availableDatesResponse;
};
