import { schema } from 'normalizr';
import { TreatmentType, Treatment } from './types';

export const treatment = new schema.Entity<Treatment>('treatments', undefined, {
  idAttribute: 'bookingServiceId',
});
export const treatments = new schema.Array<Treatment>(treatment);
treatment.define({ additionalFields: treatments });

export const treatmentType = new schema.Entity<TreatmentType>('treatmentTypes', {
  treatments,
});
export const treatmentTypes = new schema.Array<TreatmentType>(treatmentType);
