import { createReducer } from 'deox';
import { User } from './types';
import {
  setUserToken,
  fetchCurrentUserRequest,
  fetchCurrentUserSuccess,
  fetchCurrentUserError,
  logoutSuccess,
  loginSuccess,
  loginError,
  loginRequest,
} from './actions';

interface State {
  isLoading: boolean;
  token: string | null;
  user: User | null;
  errorMessage: string | null;
  productCount: number;
}

const defaultState: State = {
  isLoading: false,
  token: null,
  user: null,
  errorMessage: null,
  productCount: 0,
};

const userReducer = createReducer(defaultState, handle => [
  handle(setUserToken, (state, { payload }) => ({
    ...state,
    token: payload.token,
  })),

  handle(logoutSuccess, state => ({
    ...defaultState,
  })),

  handle(fetchCurrentUserRequest, state => ({
    ...state,
    isLoading: true,
  })),

  handle(fetchCurrentUserSuccess, (state, { payload }) => ({
    ...state,
    isLoading: false,
    user: { ...payload.user },
  })),

  handle(fetchCurrentUserError, state => ({
    ...state,
    isLoading: false,
  })),

  handle(loginRequest, state => ({
    ...state,
    isLoading: true,
    error: null,
  })),

  handle(loginSuccess, (state, { payload }) => ({
    ...state,
    isLoading: false,
    error: null,
    user: { ...payload.user },
  })),

  handle(loginError, (state, { payload }) => ({
    ...state,
    isLoading: false,
    errorMessage: payload.errorMessage,
  })),
]);

export default userReducer;
