import { configureScope, addBreadcrumb } from '@sentry/browser';
import { Dispatch, MiddlewareAPI } from 'redux';
import { RootState } from '../../root.reducer';
import errorTracking from './errorTracking';

const errorTrackingMiddleware = (middleware: MiddlewareAPI<Dispatch<any>, RootState>) => {
  configureScope(scope => {
    scope.addEventProcessor(event => {
      const state = middleware.getState();
      const name = state.user ? state.user.user : '';

      return {
        ...event,
        extra: {
          ...event.extra,
          'redux:state': state,
        },
        user: {
          ...event.user,
          name,
        },
      };
    });
  });

  return (next: Dispatch) => (action: any) => {
    addBreadcrumb({
      category: 'redux-action',
      message: action.type,
    });

    // Log all error types
    if (action.type.includes('_ERROR')) {
      errorTracking.captureException(action.payload.error);
    }

    return next(action);
  };
};

export default errorTrackingMiddleware;
