import { ITrack } from './types';

declare global {
  interface Window {
    dataLayer: any;
  }
}
const isBrowser = typeof window !== 'undefined' && window;

if (isBrowser) {
  window.dataLayer = window.dataLayer || [];
}

export default {
  trackEvent: (track: ITrack) => {
    window.dataLayer.push(track);
  },
};
