import { UserResponse, User, CreateGuestResponse } from './types';

export const mapToUser = ({
  _id,
  isActive,
  isFinished,
  points,
  phoneNumber,
  referralCode,
  name,
  email,
  role,
  bookings,
  paymentInfo,
  privacyPolicyStatus,
}: UserResponse): User => ({
  id: _id,
  isActive,
  isFinished,
  points,
  phoneNumber,
  referralCode,
  name,
  email,
  role,
  bookings: bookings.map(booking => ({
    duration: booking.duration,
    time: booking.time,
    waitlistId: booking.waitlistId,
    serviceIds: booking.services,
  })),
  paymentInfo,
  privacyPolicyStatus,
});

export const mapToCreateGuestResponse = (response: any): CreateGuestResponse => {
  return response;
};
