import { createReducer } from 'deox';
import {
  selectTreatment,
  deselectTreatment,
  selectDate,
  selectTime,
  updateCustomerInfo,
  bookAppointmentRequest,
  bookAppointmentSuccess,
  bookAppointmentError,
  resetBookingForm,
  selectAdditionalTreatment,
  deselectAdditionalTreatment,
  selectTreatmentBookingIds,
  toggleUsePoints,
  toggleShowAddedPaymentInfoSuccess,
  setBookingSection,
  setEditMode,
  setWaitlistId,
  populateExistingBooking,
} from './actions';
import { BookingSectionIDs } from './types';

export interface State {
  treatments: {
    selectedIds: Array<string>;
  };
  date: number | null;
  time: number | null;
  customerInfo: {
    name: string;
    phoneNumber: string;
    email: string;
  };
  usePoints: boolean;
  submission: {
    isLoading: boolean;
    error: Error | null;
  };
  ui: {
    showAddedPaymentInfoSuccess: boolean;
    bookingSectionSelected: string;
  };
  id: string | null;
  waitlistId: string | null;
  editMode: boolean;
}

const defaultState: State = {
  treatments: {
    selectedIds: [],
  },
  date: null,
  time: null,
  customerInfo: {
    name: '',
    phoneNumber: '',
    email: '',
  },
  usePoints: false,
  submission: {
    isLoading: false,
    error: null,
  },
  ui: {
    showAddedPaymentInfoSuccess: false,
    bookingSectionSelected: BookingSectionIDs.TreatmentSelection,
  },
  id: null,
  waitlistId: null,
  editMode: false,
};

const bookingReducer = createReducer(defaultState, handle => [
  handle(selectTreatment, (state, { payload }) => ({
    ...state,
    treatments: {
      ...state.treatments,
      selectedIds: [...state.treatments.selectedIds, payload.treatment.bookingServiceId],
    },
  })),
  handle(deselectTreatment, (state, { payload }) => {
    const newSelectedIds = state.treatments.selectedIds;
    const index = state.treatments.selectedIds.indexOf(payload.treatment.bookingServiceId);
    newSelectedIds.splice(index, 1);
    return {
      ...state,
      treatments: {
        ...state.treatments,
        selectedIds: newSelectedIds,
      },
    };
  }),
  handle(selectAdditionalTreatment, (state, { payload }) => ({
    ...state,
    treatments: {
      ...state.treatments,
      selectedIds: [
        // Replace the parent treatment from the list of selected treatments
        ...state.treatments.selectedIds.filter(
          id => id != payload.parentTreatment.bookingServiceId
        ),
        payload.additionalTreatment.bookingServiceId,
      ],
    },
  })),
  handle(deselectAdditionalTreatment, (state, { payload }) => ({
    ...state,
    treatments: {
      ...state.treatments,
      selectedIds: [
        // Replace the additional treatment from the list of selected treatments
        ...state.treatments.selectedIds.filter(
          id => id != payload.additionalTreatment.bookingServiceId
        ),
        payload.parentTreatment.bookingServiceId,
      ],
    },
  })),
  handle(selectTreatmentBookingIds, (state, { payload }) => ({
    ...state,
    treatments: {
      ...state.treatments,
      selectedIds: [...state.treatments.selectedIds, ...payload.treatmentBookingIds],
    },
  })),
  handle(selectDate, (state, { payload }) => ({
    ...state,
    time: null,
    date: payload.date,
  })),
  handle(updateCustomerInfo, (state, { payload }) => ({
    ...state,
    customerInfo: {
      ...state.customerInfo,
      ...payload,
    },
  })),
  handle(selectTime, (state, { payload }) => ({
    ...state,
    time: payload.time,
  })),
  handle(setEditMode, (state, { payload }) => ({
    ...state,
    editMode: payload.mode,
    id: payload.id,
  })),
  handle(setWaitlistId, (state, { payload }) => ({
    ...state,
    waitlistId: payload.waitlistId,
  })),
  handle(populateExistingBooking, (state, { payload }) => ({
    ...state,
    editMode: payload.editMode,
    id: payload.id,
    waitlistId: payload.waitlistId,
    time: payload.time,
    date: payload.date,
  })),
  handle(toggleUsePoints, (state, { payload }) => ({
    ...state,
    usePoints: payload.usePoints,
  })),
  handle(toggleShowAddedPaymentInfoSuccess, (state, { payload }) => ({
    ...state,
    ui: {
      ...state.ui,
      showAddedPaymentInfoSuccess: payload.showAddedPaymentInfoSuccess,
    },
  })),
  handle(setBookingSection, (state, { payload }) => ({
    ...state,
    ui: {
      ...state.ui,
      bookingSectionSelected: payload.bookingSectionId,
    },
  })),
  handle(bookAppointmentRequest, state => ({
    ...state,
    submission: {
      ...defaultState.submission,
      isLoading: true,
    },
  })),
  handle(bookAppointmentSuccess, state => ({
    ...state,
    submission: {
      ...defaultState.submission,
    },
  })),
  handle(bookAppointmentError, (state, { payload }) => ({
    ...state,
    submission: {
      ...defaultState.submission,
      error: payload.error,
    },
  })),
  handle(resetBookingForm, () => ({
    ...defaultState,
  })),
]);

export default bookingReducer;
