import { Booking, Service } from '../../user/forces/types';

export const mapResponseToBooking = (response: { [key: string]: any }): Booking => {
  const serviceIds = response.serviceIds.map((service: Service) => ({
    id: service.id,
    title: service.name,
    duration: service.duration,
    price: service.price,
  }));
  return {
    id: response.id,
    serviceIds,
    time: response.time,
    timeOfDayinMilliseconds: response.timeOfDayinMilliseconds,
    waitlistId: response.waitlistId,
    duration: response.duration,
  };
};

export const mapResponseToBookings = (response: Array<{ [key: string]: any }>): Array<Booking> => {
  return response.map(mapResponseToBooking);
};
