import { RootState } from '../../../root.reducer';

export const getAvailableDates = (state: RootState) => state.clinic.availability;
export const getAvailableDate = (state: RootState, date: string) => {
  return state.clinic.availability.availableDays[date];
};
export const isAvailableDatesLoading = (state: RootState) => state.clinic.isLoading;
export const hasLoadedAvailableDates = (state: RootState, toDate: string, fromDate: string) => {
  const hasFromDate = getAvailableDate(state, fromDate) != null;
  const hasToDate = getAvailableDate(state, toDate) != null;
  return hasFromDate && hasToDate;
};
