import { getSelectedTreatments } from './../../booking/forces/selectors';
import { Dispatch } from 'redux';
import { createActionCreator } from 'deox';
import { RootState } from '../../../root.reducer';
import { ThunkArguments } from '../../../root.types';
import api from './api';
import { mapResponseToAvailableDates } from './mappers';
import { AvailableDatesResponse } from './types';
import { hasLoadedAvailableDates } from './selectors';

export const getAvailabilityRequest = createActionCreator('CLINIC_GET_AVAILABILITY_REQUEST');
export const getAvailabilitySuccess = createActionCreator(
  'CLINIC_GET_AVAILABILITY_SUCCESS',

  resolve => (availableDatesResponse: AvailableDatesResponse) => {
    return resolve({ availableDatesResponse });
  }
);

export const getAvailabilityError = createActionCreator(
  'CLINIC_GET_AVAILABILITY_ERROR',
  resolve => (error: Error) => {
    return resolve({ error });
  }
);

export const getAvailability = ({
  from,
  to,
  appointmentId,
}: { from?: string; to?: string; appointmentId?: string } = {}) => {
  return async (
    dispatch: Dispatch,
    getState: () => RootState,
    { request, config }: ThunkArguments
  ) => {
    const state = getState();
    // Don't get available if it's already cached in state
    if (from != null && to != null) {
      if (hasLoadedAvailableDates(state, from, to)) {
        return;
      }
    }

    const selectedTreatments = getSelectedTreatments(state);
    dispatch(getAvailabilityRequest());
    try {
      const response = await api.getAvailability(request)({
        clinicId: config.defaultClinicId,
        treatments: selectedTreatments,
        from,
        to,
        appointmentId,
      });
      const availableDates = mapResponseToAvailableDates(response);
      dispatch(getAvailabilitySuccess(availableDates));
    } catch (err) {
      dispatch(getAvailabilityError(err));
    }
  };
};
