import * as React from 'react';
import { Provider } from 'react-redux';
import store from './index';
import { appSetup } from '../../domain/app/forces/actions';
import Snackbar from '../../domain/ui/containers/Snackbar';

store.dispatch(appSetup());

type Props = {
  element: any;
};

const ReduxProvider: React.FunctionComponent<Props> = ({ element }) => {
  return (
    <Provider store={store}>
      {element}
      <Snackbar />
    </Provider>
  );
};

export default ReduxProvider;
