import sanityClient from '@sanity/client';
import Config from '../../../config';
import { mapToTreatmentTypes } from './mappers';

async function makeRequest(query: string) {
  return await sanityClient({
    projectId: Config.sanity.projectId,
    dataset: Config.sanity.dataset,
    useCdn: false,
  }).fetch(query);
}

export const getAllSanityTreatmentTypes = async () => {
  const query = `*[_type == "treatmentType" ] | order(order) {
    _id,
    title,
    hidden,
    treatments[]-> {
      additionalFields[]-> {
        ...
      },
      ...
    }
  }`;
  try {
    const response = await makeRequest(query);
    return mapToTreatmentTypes(response);
  } catch (err) {
    console.log('error in getAllSanityTreatmentTypes(): ', err);
    throw new Error('Error in fetching all treatment types from sanity');
  }
};
