import { Component } from 'react';
import ReactDOM from 'react-dom';

type OwnProps = {};
type Props = OwnProps & React.HTMLAttributes<HTMLElement>;

const isBrowser = typeof document !== `undefined`;
const portalRoot = isBrowser ? document.getElementById('portal') : null;

class Portal extends Component<Props> {
  el: HTMLDivElement | null;

  constructor(props: Props) {
    super(props);
    this.el = isBrowser ? document.createElement('div') : null;
  }

  componentDidMount = () => {
    if (portalRoot && this.el) {
      portalRoot.appendChild(this.el);
    }
  };

  componentWillUnmount = () => {
    if (portalRoot && this.el) {
      portalRoot.removeChild(this.el);
    }
  };

  render() {
    const { children } = this.props;

    if (this.el) {
      return ReactDOM.createPortal(children, this.el);
    }
    return null;
  }
}

export default Portal;
