import { createReducer } from 'deox';
import {
  fetchBookingsRequest,
  fetchBookingsSuccess,
  fetchBookingsError,
  fetchBookingRequest,
  fetchBookingSuccess,
  fetchBookingError,
  cancelBookingRequest,
  cancelBookingSuccess,
  cancelBookingError,
} from './actions';
import { Booking } from '../../user/forces/types';

interface State {
  isLoading: boolean;
  error: Error | null;
  bookings: Array<Booking>;
}

const defaultState: State = {
  bookings: [],
  isLoading: false,
  error: null,
};

export default createReducer(defaultState, handle => [
  handle(fetchBookingsRequest, state => ({
    ...state,
    isLoading: true,
    error: null,
  })),
  handle(fetchBookingsSuccess, (state, { payload }) => {
    const bookings = [...payload.bookings];
    return {
      ...state,
      bookings,
      isLoading: false,
      error: null,
    };
  }),
  handle(fetchBookingsError, (state, { payload }) => ({
    ...defaultState,
    isLoading: true,
    error: payload.error,
  })),

  handle(fetchBookingRequest, state => ({
    ...state,
    isLoading: true,
    error: null,
  })),
  handle(fetchBookingSuccess, (state, { payload }) => {
    const bookingExists = state.bookings.find(booking => booking.id === payload.booking.id);
    const bookings = bookingExists ? state.bookings : [...state.bookings, payload.booking];
    return {
      ...state,
      bookings,
      isLoading: false,
      error: null,
    };
  }),
  handle(fetchBookingError, (state, { payload }) => ({
    ...state,
    isLoading: true,
    error: payload.error,
  })),

  handle(cancelBookingRequest, state => ({
    ...state,
    isLoading: true,
    error: null,
  })),
  handle(cancelBookingSuccess, (state, { payload }) => {
    return {
      ...state,
      bookings: state.bookings.filter(booking => booking.id != payload.id),
      isLoading: false,
      error: null,
    };
  }),
  handle(cancelBookingError, (state, { payload }) => ({
    ...state,
    isLoading: true,
    error: payload.error,
  })),
]);
