import { APIRequest } from '../../../root.types';
import { Treatment } from '../../treatment/forces/types';
import { mapTreatmentsToBookingServiceIds } from './api.utils';
import { stringifyObjectWithBrackets } from '../../../utils/url.utils';

const getAvailability = (request: APIRequest) => async (parameters: {
  treatments: Array<Treatment>;
  clinicId: string;
  to?: string;
  from?: string;
  appointmentId?: string;
}) => {
  const bookingServiceIds = mapTreatmentsToBookingServiceIds(parameters.treatments);
  const treatmentQueryParameters = stringifyObjectWithBrackets('treatments', bookingServiceIds);
  if (parameters.from && parameters.to) {
    if (parameters.appointmentId) {
      return request({
        path: `/clinics/${parameters.clinicId}/availability?${treatmentQueryParameters}&from=${
          parameters.from
        }&to=${parameters.to}&appointmentId=${parameters.appointmentId}`,
      });
    }
    return request({
      path: `/clinics/${parameters.clinicId}/availability?${treatmentQueryParameters}&from=${
        parameters.from
      }&to=${parameters.to}`,
    });
  }

  if (parameters.appointmentId) {
    return request({
      path: `/clinics/${
        parameters.clinicId
      }/availability?${treatmentQueryParameters}&appointmentId=${parameters.appointmentId}`,
    });
  }
  return request({
    path: `/clinics/${parameters.clinicId}/availability?${treatmentQueryParameters}`,
  });
};

export default {
  getAvailability,
};
