import queryString from 'query-string';

export const stringifyObjectWithBrackets = (key: string, object: any): string => {
  return queryString.stringify({ [key]: object }, { arrayFormat: 'bracket' });
};

export const parseQueryString = (string: string) => {
  return queryString.parse(string, { arrayFormat: 'bracket' });
};

export const getCurrentHostname = () => {
  const { protocol, hostname, port } = window.location;
  const portPart = port && `:${port}`;
  return `${protocol}//${hostname}${portPart}`;
};
