import { RootState } from '../../../root.reducer';
import { UserRole, User } from './types';
import { windowExist } from '../../../utils/general.utils';

export const isLoggedIn = (state: RootState) => getCurrentUser(state) != null;
export const getCurrentUser = (state: RootState) => state.user.user;
export const userdataIsLoading = (state: RootState) => state.user.isLoading;
export const isGuestLoggedIn = (state: RootState) => isLoggedIn(state);
export const isUserGuest = (state: RootState): boolean => {
  const currentUser = getCurrentUser(state);
  return !!currentUser && currentUser.role === UserRole.Guest;
};
export const isMemberLoggedIn = (state: RootState) => isLoggedIn(state) && !isUserGuest(state);
export const getUserBookings = (state: RootState) => {
  const currentUser = getCurrentUser(state);
  if (currentUser == null) {
    return [];
  }
  return currentUser.bookings;
};
export const isMember = (state: RootState) => {
  const currentUser = getCurrentUser(state);
  return currentUser ? currentUser.role === UserRole.Member : false;
};
export const getBookingByID = (state: RootState, id: string) => {
  const currentUser = getCurrentUser(state);
  if (currentUser == null) {
    return null;
  }
  return getUserBookings(state).find(booking => booking.publicId == id);
};

export const getNameFromBooking = (state: RootState) => {
  const currentUser = getCurrentUser(state);
  return currentUser ? currentUser.name : '';
};
export const hasToken = (state: RootState) => getToken(state) != null;
export const getToken = (state: RootState) => state.user.token;
export const getReferralCode = (state: RootState) => state.user.referral.code;
export const getReferralName = (state: RootState) => state.user.referral.name;
export const hasAddedPaymentInfo = (state: RootState) =>
  state.user.user && state.user.user.isActive;
export const hasPaymentInfo = (state: RootState) => {
  const user = getCurrentUser(state);
  return user && user.paymentInfo && user.paymentInfo.cardId ? true : false;
};
export const getPaymentInfo = (state: RootState) => {
  const user = getCurrentUser(state);
  if (user && user.paymentInfo) return user.paymentInfo.cardId ? user.paymentInfo : null;
  else return null;
};
export const getPrivacyPolicyStatus = (state: RootState) => {
  let guestMode;
  if (windowExist()) {
    guestMode = localStorage.getItem('privacy_policy_status');
  }
  if (state.user.user !== null) {
    return state.user.user.privacyPolicyStatus;
  } else if (guestMode) {
    return true;
  }
  return false;
};
